import React, { useContext, useState } from "react";
import { UserContext } from "../App";

import {
  CommonInput,
  Container,
  FileInput,
  Input,
  InputContainer,
  Label,
  PhotoButton,
  PhotoContainer,
  RedSpan,
  Span,
  Textarea,
  Wrapper,
} from "./Styles";

export default function PersonalInfo({ register, errors }) {
  const { setUser } = useContext(UserContext);
  return (
    <Wrapper>
      <Container>
        <InputContainer>
          <Label></Label>
          <Input
            type="text"
            name="name"
            {...register("name")}
            onChange={(e) => setUser({ firstName: e.target.value })}
          />
          {errors.name ? (
            <RedSpan> 2 ,  </RedSpan>
          ) : (
            <Span> 2 ,  </Span>
          )}
        </InputContainer>

        <InputContainer>
          <Label></Label>
          <Input
            type="text"
            name="surname"
            {...register("surname")}
            onChange={(e) => setUser({ lastName: e.target.value })}
          />
          {errors.surname ? (
            <RedSpan> 2 ,  </RedSpan>
          ) : (
            <Span> 2 ,  </Span>
          )}
        </InputContainer>
      </Container>

      <PhotoContainer>
        <Label>  </Label>
        <FileInput
          type="file"
          name="image"
          {...register("image")}
          accept="image/*"
          onChange={(e) => setUser({ image: e.target.value })}
        />

        <PhotoButton></PhotoButton>
      </PhotoContainer>

      <InputContainer>
        <Label> ()</Label>
        <Textarea
          name="about_me"
          {...register("about_me")}
          onChange={(e) => setUser({ about: e.target.value })}
        ></Textarea>
      </InputContainer>
      <InputContainer>
        <Label> </Label>
        <CommonInput
          type="text"
          name="email"
          {...register("email")}
          onChange={(e) => setUser({ email: e.target.value })}
        />

        {errors.email ? (
          <RedSpan>  @redberry,ge-</RedSpan>
        ) : (
          <Span>  @redberry,ge-</Span>
        )}
      </InputContainer>
      <InputContainer>
        <Label> </Label>

        <CommonInput
          type="text"
          name="phone_number"
          {...register("phone_number")}
          onChange={(e) => setUser({ phone: e.target.value })}
        />

        {errors.phone_number ? (
          <RedSpan>
                 
          </RedSpan>
        ) : (
          <Span>     </Span>
        )}
      </InputContainer>
    </Wrapper>
  );
}
