import React, { useContext } from "react";
import { UserContext } from "../App";

import line from "../assets/Line 19.png";
import {
  CommonInput,
  Container,
  ExperienceButton,
  ImageContainer,
  Input,
  InputContainer,
  Label,
  Line,
  RedSpan,
  Span,
  Textarea,
  Wrapper,
} from "./Styles";

export default function Experience({ register, errors }) {
  const { setUser } = useContext(UserContext);
  return (
    <Wrapper>
      <InputContainer>
        <Label></Label>
        <CommonInput
          type="text"
          name="position"
          {...register("position")}
          onChange={(e) => setUser({ position: e.target.value })}
        />
        {errors.position ? (
          <RedSpan> 2 </RedSpan>
        ) : (
          <Span> 2 </Span>
        )}
      </InputContainer>

      <InputContainer>
        <Label></Label>
        <CommonInput
          type="text"
          name="employer"
          {...register("employer")}
          onChange={(e) => setUser({ employer: e.target.value })}
        />
        {errors.employer ? (
          <RedSpan> 2 </RedSpan>
        ) : (
          <Span> 2 </Span>
        )}
      </InputContainer>
      <Container>
        <InputContainer>
          <Label> </Label>
          <Input
            type="date"
            name="start_date"
            {...register("start_date")}
            onChange={(e) => setUser({ startDate: e.target.value })}
          />
        </InputContainer>
        <InputContainer>
          <Label> </Label>
          <Input
            type="date"
            name="due_date"
            {...register("due_date")}
            onChange={(e) => setUser({ dueDate: e.target.value })}
          />
        </InputContainer>
      </Container>

      <InputContainer>
        <Label></Label>
        <Textarea
          name="description"
          {...register("description")}
          onChange={(e) => setUser({ description: e.target.value })}
        ></Textarea>
      </InputContainer>
      <ImageContainer>
        <Line src={line} />
      </ImageContainer>
      <ExperienceButton>  </ExperienceButton>
    </Wrapper>
  );
}
